import React, { useState, useEffect } from 'react';
import './AdminDashboard.css';

function AdminDashboard() {
  const [activeTab, setActiveTab] = useState('menu');
  const [menuItems, setMenuItems] = useState([]);
  const [events, setEvents] = useState([]);

  const [newMenuItem, setNewMenuItem] = useState({ name: '', price: '', description: '', category: '' });
  const [newEvent, setNewEvent] = useState({ title: '', date: '', time: '', description: '', category: '' });

  const [selectedMenuImage, setSelectedMenuImage] = useState(null);
  const [selectedEventImage, setSelectedEventImage] = useState(null);

  const [editingMenuId, setEditingMenuId] = useState(null);
  const [editMenuItem, setEditMenuItem] = useState({ name: '', price: '', description: '', category: '' });

  const [editingEventId, setEditingEventId] = useState(null);
  const [editEvent, setEditEvent] = useState({ title: '', date: '', time: '', description: '', category: '' });

  const categories = ["Vegetarian Starters", "Non-Vegetarian Starters", "Soups & Salads", "Rice & Biryani", "Main Course", "Indian Breads", "Accompaniments", "Desserts", "Beverages"];
  const eventCategories = ['Music', 'Tasting', 'Workshop', 'Special'];

  useEffect(() => {
    fetchMenuItems();
    fetchEvents();
  }, []);

  const fetchMenuItems = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/get_menu.php`);
      const data = await res.json();
      setMenuItems(data);
    } catch (err) { console.error(err); }
  };

  const fetchEvents = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/get_events.php`);
      const data = await res.json();
      setEvents(data);
    } catch (err) { console.error(err); }
  };

  const handleAddMenuItem = async () => {
    try {
      const formData = new FormData();
      Object.entries(newMenuItem).forEach(([key, value]) => formData.append(key, value));
      if (selectedMenuImage) formData.append('image', selectedMenuImage);

      const res = await fetch(`${process.env.REACT_APP_API_URL}/add_item.php`, { method: 'POST', body: formData });
      const data = await res.json();
      if (data.success) {
        fetchMenuItems();
        setNewMenuItem({ name: '', price: '', description: '', category: '' });
        setSelectedMenuImage(null);
      }
    } catch (err) { console.error(err); }
  };

  const handleAddEvent = async () => {
    try {
      const formData = new FormData();
      Object.entries(newEvent).forEach(([key, value]) => formData.append(key, value));
      if (selectedEventImage) formData.append('image', selectedEventImage);

      const res = await fetch(`${process.env.REACT_APP_API_URL}/add_event.php`, { method: 'POST', body: formData });
      const data = await res.json();
      if (data.success) {
        fetchEvents();
        setNewEvent({ title: '', date: '', time: '', description: '', category: '' });
        setSelectedEventImage(null);
      }
    } catch (err) { console.error(err); }
  };

  // Edit/save menu item
  const handleEditMenuItem = (item) => {
    setEditingMenuId(item.id);
    setEditMenuItem(item);
  };

  const handleSaveMenuItem = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/update_item.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: editingMenuId, ...editMenuItem })
      });
      const data = await res.json();
      if (data.success) {
        fetchMenuItems();
        setEditingMenuId(null);
      }
    } catch (err) { console.error(err); }
  };

  const handleDeleteMenuItem = async (id) => {
    if (!window.confirm('Delete this menu item?')) return;
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/delete_item.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id })
      });
      const data = await res.json();
      if (data.success) fetchMenuItems();
    } catch (err) { console.error(err); }
  };

  // Edit/save event
  const handleEditEvent = (event) => {
    setEditingEventId(event.id);
    setEditEvent(event);
  };

  const handleSaveEvent = async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/update_event.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: editingEventId, ...editEvent })
      });
      const data = await res.json();
      if (data.success) {
        fetchEvents();
        setEditingEventId(null);
      }
    } catch (err) { console.error(err); }
  };

  const handleDeleteEvent = async (id) => {
    if (!window.confirm('Delete this event?')) return;
    try {
      const res = await fetch(`${process.env.REACT_APP_API_URL}/delete_event.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id })
      });
      const data = await res.json();
      if (data.success) fetchEvents();
    } catch (err) { console.error(err); }
  };

  return (
    <div className="admin-container">
      <h2 className="admin-title">🍽 Admin Dashboard</h2>

      <div className="tab-buttons">
        <button className={activeTab === 'menu' ? 'active' : ''} onClick={() => setActiveTab('menu')}>Manage Menu</button>
        <button className={activeTab === 'events' ? 'active' : ''} onClick={() => setActiveTab('events')}>Manage Events</button>
      </div>

      {activeTab === 'menu' && (
        <>
          <div className="card add-item-card">
            <h3>Add Menu Item</h3>
            <input placeholder="Name" value={newMenuItem.name} onChange={e => setNewMenuItem({ ...newMenuItem, name: e.target.value })} />
            <input placeholder="Price" value={newMenuItem.price} onChange={e => setNewMenuItem({ ...newMenuItem, price: e.target.value })} />
            <input placeholder="Description" value={newMenuItem.description} onChange={e => setNewMenuItem({ ...newMenuItem, description: e.target.value })} />
            <select value={newMenuItem.category} onChange={e => setNewMenuItem({ ...newMenuItem, category: e.target.value })}>
              <option value="">Select Category</option>
              {categories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
            </select>
            <input type="file" onChange={e => setSelectedMenuImage(e.target.files[0])} />
            <button className="btn-primary" onClick={handleAddMenuItem}>Add Item</button>
          </div>

          <div className="menu-items-grid">
            {menuItems.map(item => (
              <div key={item.id} className="card menu-item-card">
              <img src={`${process.env.REACT_APP_API_URL}/uploads/${item.image}`} alt={item.name} />

                {editingMenuId === item.id ? (
                  <>
                    <input value={editMenuItem.name} onChange={e => setEditMenuItem({ ...editMenuItem, name: e.target.value })} />
                    <input value={editMenuItem.price} onChange={e => setEditMenuItem({ ...editMenuItem, price: e.target.value })} />
                    <input value={editMenuItem.description} onChange={e => setEditMenuItem({ ...editMenuItem, description: e.target.value })} />
                    <select value={editMenuItem.category} onChange={e => setEditMenuItem({ ...editMenuItem, category: e.target.value })}>
                      {categories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
                    </select>
                    <button onClick={handleSaveMenuItem}>Save</button>
                    <button onClick={() => setEditingMenuId(null)}>Cancel</button>
                  </>
                ) : (
                  <>
                    <h4>{item.name} <span>₹{item.price}</span></h4>
                    <p>{item.description}</p>
                    <span className="badge">{item.category}</span>
                    <button onClick={() => handleEditMenuItem(item)}>Edit</button>
                    <button onClick={() => handleDeleteMenuItem(item.id)}>Delete</button>
                  </>
                )}
              </div>
            ))}
          </div>
        </>
      )}

      {activeTab === 'events' && (
        <>
          <div className="card add-item-card">
            <h3>Add Event</h3>
            <input placeholder="Title" value={newEvent.title} onChange={e => setNewEvent({ ...newEvent, title: e.target.value })} />
            <input type="date" value={newEvent.date} onChange={e => setNewEvent({ ...newEvent, date: e.target.value })} />
            <input type="time" value={newEvent.time} onChange={e => setNewEvent({ ...newEvent, time: e.target.value })} />
            <input placeholder="Description" value={newEvent.description} onChange={e => setNewEvent({ ...newEvent, description: e.target.value })} />
            <select value={newEvent.category} onChange={e => setNewEvent({ ...newEvent, category: e.target.value })}>
              <option value="">Select Category</option>
              {eventCategories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
            </select>

            <button className="btn-primary" onClick={handleAddEvent}>Add Event</button>
          </div>

          <div className="events-list">
            {events.map(event => (
              <div key={event.id} className="card event-card">
                
                {editingEventId === event.id ? (
                  <>
                    <input value={editEvent.title} onChange={e => setEditEvent({ ...editEvent, title: e.target.value })} />
                    <input type="date" value={editEvent.date} onChange={e => setEditEvent({ ...editEvent, date: e.target.value })} />
                    <input type="time" value={editEvent.time} onChange={e => setEditEvent({ ...editEvent, time: e.target.value })} />
                    <input value={editEvent.description} onChange={e => setEditEvent({ ...editEvent, description: e.target.value })} />
                    <select value={editEvent.category} onChange={e => setEditEvent({ ...editEvent, category: e.target.value })}>
                      {eventCategories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
                    </select>
                    <button onClick={handleSaveEvent}>Save</button>
                    <button onClick={() => setEditingEventId(null)}>Cancel</button>
                  </>
                ) : (
                  <>
                    <h4>{event.title}</h4>
                    <p><strong>{event.date}</strong> at <strong>{event.time}</strong></p>
                    <p>{event.description}</p>
                    <span className="badge">{event.category}</span>
                    <button onClick={() => handleEditEvent(event)}>Edit</button>
                    <button onClick={() => handleDeleteEvent(event.id)}>Delete</button>
                  </>
                )}
              </div>
            ))}
          </div>
        </>
      )}
    </div>
  );
}

export default AdminDashboard;
