// src/App.js
import React, { useState, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './App.css';

import NavbarA from './components/NavbarA';
import NavbarB from './components/NavbarB';
import Home from './components/Home';
import Menu from './components/Menu';
import Reservation from './components/Reservation';
import About from './components/About';
import Contact from './components/Contact';
import Events from './components/Events';
import Gallery from './components/Gallery';
import Footer from './components/Footer';
import AdminDashboard from './components/AdminDashboard';
import Login from './components/Login';
import ProtectedRoute from './components/ProtectedRoute';
import SplashScreen from './components/SplashScreen';

function App() {
  const [showAltNavbar, setShowAltNavbar] = useState(false);
  const [entered, setEntered] = useState(false);

  useEffect(() => {
    AOS.init({ duration: 1000, once: true });
  }, []);

  useEffect(() => {
    const onScroll = () => {
      setShowAltNavbar(window.scrollY > window.innerHeight * 0.8);
      AOS.refresh();
    };
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  if (!entered) return <SplashScreen onEnter={() => setEntered(true)} />;

  return (
    <div className="App">
      <Routes>
        <Route path="/" element={
          <>
            {showAltNavbar ? <NavbarB /> : <NavbarA />}
            <div id="home"><Home /></div>
            <Menu />
            <div id="reservation"><Reservation /></div>
            <div id="about"><About /></div>
            <div id="contact"><Contact /></div>
            <div id="events"><Events /></div>
            <div id="gallery"><Gallery /></div>
            <Footer />
          </>
        }/>
        <Route path="/admin" element={
          <ProtectedRoute>
            <AdminDashboard />
          </ProtectedRoute>
        }/>
        <Route path="/login" element={<Login />} />
      </Routes>
    </div>
  );
}

export default App;
