import React, { useEffect, useState } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { ToastContainer, toast } from 'react-toastify';

const Events = () => {
  const [events, setEvents] = useState([]);
  const [filter, setFilter] = useState('All');
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [formData, setFormData] = useState({ name: '', email: '' });

  useEffect(() => {
    AOS.init({ duration: 800 });
    fetchEvents();

    // Allow closing modal with Escape key
    const handleEsc = e => {
      if (e.key === 'Escape') setSelectedEvent(null);
    };
    window.addEventListener('keydown', handleEsc);
    return () => window.removeEventListener('keydown', handleEsc);
  }, []);

  const fetchEvents = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/get_events.php`);
      const data = await response.json();
      setEvents(data);
    } catch (err) {
      console.error('Failed to fetch events:', err);
      toast.error('Failed to load events');
    }
  };

  const filteredEvents = filter === 'All'
    ? events
    : events.filter(event => event.category === filter);

  const handleRegister = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/event_registration.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          event_id: selectedEvent.id,
          name: formData.name,
          email: formData.email
        })
      });
      const data = await response.json();
      if (data.success) {
        toast.success(`Successfully registered for "${selectedEvent.title}"!`);
        setTimeout(() => {
          setSelectedEvent(null);
          setFormData({ name: '', email: '' });
        });
      } else {
        toast.error('Registration failed. Please try again!');
      }
    } catch (error) {
      console.error(error);
      toast.error('An unexpected error occurred.');
    }
  };

  return (
    <section className="events-section">
      <ToastContainer position="bottom-right" autoClose={3000} theme="colored"/>
      <h2 className="events-heading" data-aos="fade-down">Upcoming Events</h2>

      <div className="event-filters" data-aos="fade-up">
        {['All', 'Music', 'Tasting', 'Workshop'].map(cat => (
          <button
            key={cat}
            className={`filter-chip ${filter === cat ? 'active' : ''}`}
            onClick={() => setFilter(cat)}
          >
            {cat}
          </button>
        ))}
      </div>

      <div className="event-grid">
        {filteredEvents.length > 0 ? filteredEvents.map(event => (
          <div
            key={event.id}
            className="event-card"
            data-aos="zoom-in"
            onClick={() => setSelectedEvent(event)}
          >
            
            <div className="event-card-content">
              <h3>{event.title}</h3>
              <p><strong>{event.date}</strong> • {event.time}</p>
              <p className="event-description">{event.description}</p>
              <span className="badge">{event.category}</span>
            </div>
          </div>
        )) : (
          <p className="no-events-text" data-aos="fade-up">No events in this category right now.</p>
        )}
        
      </div>

      {selectedEvent && (
        <div className="event-modal">
          
          <div className="modal-content" data-aos="zoom-in">
            <span className="close-btn" onClick={() => setSelectedEvent(null)}>×</span>
            <h2>{selectedEvent.title}</h2>
            <p><strong>Date:</strong> {selectedEvent.date}</p>
            <p><strong>Time:</strong> {selectedEvent.time}</p>
            <p>{selectedEvent.description}</p>
            <form>
              <input
                type="text"
                placeholder="Your Name" required
                value={formData.name}
                onChange={e => setFormData({ ...formData, name: e.target.value })}
              />
              <input
                type="email"
                placeholder="Your Email" required
                value={formData.email}
                onChange={e => setFormData({ ...formData, email: e.target.value })}
              />
            </form>
            <div className="modal-actions">
              <button className="btn-primary" onClick={handleRegister}>Register</button>
              <button className="btn-secondary" onClick={() => setSelectedEvent(null)}>Cancel</button>
            </div>
          </div>
        </div>
      )}
    </section>
  );
};

export default Events;
