// src/components/Reservation.js
import React, { useState } from 'react';
import { toast, ToastContainer } from 'react-toastify';

const Reservation = () => {
  const [form, setForm] = useState({ name: '', email: '', date: '', time: '', guests: 1 });

  const handleChange = e => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async e => {
    e.preventDefault();

    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/reservation.php`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(form)
      });
      const data = await response.json();

      if (data.success) {
        toast.success('Reservation saved successfully!');
        setForm({ name: '', date: '', time: '', guests: 1 });
      } else {
        toast.error(`Failed: ${data.message}`);
      }
    } catch (error) {
      console.error('Error:', error);
      toast.error('Server error, please try again later.');
    }
  };

  return (
    <section className="reservation-section" id="reservation" data-aos="fade-up">
      <div className="reservation-container">
        <ToastContainer position="bottom-right" autoClose={3000} theme="colored" />
        <h2 className="reservation-title">Book a Table</h2>
        <form className="reservation-form" onSubmit={handleSubmit}>
          <input type="text" name="name" placeholder="Your Name" value={form.name} required onChange={handleChange} data-aos="fade-up"/>
          <input type="date" name="date" value={form.date} required onChange={handleChange} data-aos="fade-down" />
          <input type="time" name="time" value={form.time} required onChange={handleChange} data-aos="fade-up"/>
          <input type="email" name="email" placeholder="Your Email" value={form.email} required onChange={handleChange} data-aos="fade-down" />
          <input type="number" name="guests" min="1" max="20" value={form.guests} onChange={handleChange} required data-aos="fade-up" />
          <button type="submit" data-aos="fade-down">Reserve Now</button>
        </form>
      </div>
    </section>
  );
};

export default Reservation;
