import React, { useState, useEffect } from 'react';
import './Menu.css';

const Menu = () => {
  const [menuItems, setMenuItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState('Vegetarian Starters');
  const [error, setError] = useState(null);

 const categories = ["Vegetarian Starters", "Non-Vegetarian Starters", "Soups & Salads", "Rice & Biryani", "Main Course", "Indian Breads", "Accompaniments", "Desserts", "Beverages"];

  useEffect(() => {
    fetch(`${process.env.REACT_APP_API_URL}/get_menu.php`)
      .then(res => res.json())
      .then(data => {
        setMenuItems(data);
        setLoading(false);
      })
      .catch(err => {
        console.error('Failed to fetch menu:', err);
        setError('Failed to load menu. Please try again later.');
        setLoading(false);
      });
  }, []);

  const filteredItems = selectedCategory === 'All'
    ? menuItems
    : menuItems.filter(item => item.category === selectedCategory);

  return (
    <section className="menu-section" id="menu">
      <h2 className="special-menu-title" data-aos="fade-up">Special Menu</h2>

      <div className="category-carousel">
        <div className="carousel-track">
          {categories.map(cat => (
            <button
              key={cat}
              onClick={() => setSelectedCategory(cat)}
              className={`carousel-chip ${selectedCategory === cat ? 'active' : ''}`}
            >
              {cat}
            </button>
          ))}
        </div>
      </div>

      {loading && <p className="loading-text">Loading menu...</p>}

      {error && <p className="error-text">{error}</p>}

      {!loading && !error && (
        <>
          {filteredItems.length === 0 ? (
            <p className="no-items-text">No items found in this category.</p>
          ) : (
            <div className="menu-list">
              {filteredItems.map((item, index) => (
                <div
                  className="menu-card"
                  key={index}
                  data-aos="zoom-in"
                  data-aos-delay="100"
                >
                  <div className="image-container">
                    <img
                      src={`http://localhost/myapp/backend/uploads/${item.image}`}
                      alt={item.name}
                      className="menu-image"
                    />
                    <span className="category-badge">{item.category}</span>
                  </div>
                  <div className="menu-details">
                    <div className="menu-header">
                      <h3 className="menu-title">{item.name}</h3>
                      <span className="price-badge">₹ {item.price}</span>
                    </div>
                    <p className="menu-description">{item.description}</p>
                  </div>
                </div>
              ))}
            </div>
          )}
        </>
      )}
    </section>
  );
};

export default Menu;
